export default {
	data() {
		return {
			ColorList: this.ColorList,
			color: 'red',
			loading: false,
			modalName: '',
			active: false
		};
	},
	onLoad: function() {
		let that = this;
		setTimeout(function() {
			that.loading = true
		}, 500)
	},
	methods: {
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		SetColor(e) {
			this.color = e.currentTarget.dataset.color;
			this.modalName = null
		},
		SetActive(e) {
			this.active = e.detail.value
		},
	}
}
